"use client";

import { useState, useEffect } from "react";
import { useAuth } from "../context/AuthContext";
import { toast } from "react-toastify";
import { Loader2, CheckCircle, XCircle, CreditCard } from "lucide-react";

interface Package {
  id: number;
  name: string;
  description: string;
  monthly_price: number;
  yearly_price: number;
  features: string[];
}

export default function SubscriptionPage() {
  const { currentSubscription, getSubscriptionPackages, initiateSubscriptionPayment } = useAuth();
  const [packages, setPackages] = useState<Package[]>([]);
  const [loading, setLoading] = useState(true);
  const [processingPayment, setProcessingPayment] = useState(false);
  const [selectedDuration, setSelectedDuration] = useState<{ [key: number]: 'monthly' | 'yearly' }>({});

  useEffect(() => {
    fetchPackages();
  }, []);

  const fetchPackages = async () => {
    try {
      setLoading(true);
      const pkgs = await getSubscriptionPackages();
      setPackages(pkgs.packages);
      const durations: { [key: number]: 'monthly' | 'yearly' } = {};
      pkgs.packages.forEach((pkg: Package) => {
        durations[pkg.id] = 'monthly';
      });
      setSelectedDuration(durations);
    } catch (error) {
      toast.error("Failed to load packages");
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const handleUpgrade = async (pkg: Package) => {
    if (processingPayment) return;

    if (pkg.name === currentSubscription?.SubscriptionPackage?.name) {
      toast.info("You already have this subscription");
      return;
    }

    setProcessingPayment(true);
    try {
      await initiateSubscriptionPayment(pkg.id, selectedDuration[pkg.id]);
    } catch (error) {
      toast.error("Failed to initiate payment");
    } finally {
      setProcessingPayment(false);
    }
  };


  const getPrice = (pkg: Package, duration: 'monthly' | 'yearly') => {
    return duration === 'monthly' ? pkg.monthly_price : pkg.yearly_price;
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Loader2 className="w-8 h-8 animate-spin text-indigo-600" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Choose Your Subscription</h1>
          <p className="text-lg text-gray-600">Unlock premium features for better predictions</p>
        </div>

        {currentSubscription && (
          <div className="bg-white rounded-xl shadow-md p-6 mb-12">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-xl font-semibold text-gray-900">Current Subscription</h2>
                <p className="text-gray-600">{currentSubscription.SubscriptionPackage.name}</p>
              </div>
              <div className="text-right">
                <p className="text-sm text-gray-500">Status: {currentSubscription.status}</p>
                {currentSubscription.endDate && (
                  <p className="text-sm text-gray-500">Expires: {new Date(currentSubscription.endDate).toLocaleDateString()}</p>
                )}
              </div>
            </div>
          </div>
        )}

        <div className="grid md:grid-cols-3 gap-8">
          {packages.map((pkg) => (
            <div key={pkg.id} className="bg-white rounded-xl shadow-md overflow-hidden">
              <div className="p-6 border-b">
                <h3 className="text-2xl font-bold text-gray-900 mb-2">{pkg.name}</h3>
                <p className="text-gray-600 mb-4">{pkg.description}</p>
                <div className="flex justify-center space-x-4 mb-4">
                  <button
                    onClick={() => setSelectedDuration({ ...selectedDuration, [pkg.id]: 'monthly' })}
                    className={`px-4 py-2 rounded-lg ${selectedDuration[pkg.id] === 'monthly' ? 'bg-indigo-600 text-white' : 'bg-gray-100 text-gray-700'}`}
                  >
                    Monthly
                  </button>
                  <button
                    onClick={() => setSelectedDuration({ ...selectedDuration, [pkg.id]: 'yearly' })}
                    className={`px-4 py-2 rounded-lg ${selectedDuration[pkg.id] === 'yearly' ? 'bg-indigo-600 text-white' : 'bg-gray-100 text-gray-700'}`}
                  >
                    Yearly
                  </button>
                </div>
                <div className="text-center">
                  <span className="text-3xl font-bold text-gray-900">LKR {getPrice(pkg, selectedDuration[pkg.id])}</span>
                  <span className="text-gray-600"> /{selectedDuration[pkg.id]}</span>
                </div>
              </div>
              <div className="p-6">
                <ul className="space-y-3">
                  {pkg.features.map((feature, idx) => (
                    <li key={idx} className="flex items-center space-x-3 text-gray-700">
                      <CheckCircle className="w-5 h-5 text-green-500" />
                      <span>{feature}</span>
                    </li>
                  ))}
                </ul>
              </div>
              <div className="p-6 border-t">
                <button
                  onClick={() => handleUpgrade(pkg)}
                  disabled={processingPayment || pkg.name === 'Silver'}
                  className="w-full flex items-center justify-center space-x-2 px-4 py-3 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 disabled:opacity-50 transition"
                >
                  {processingPayment ? <Loader2 className="w-5 h-5 animate-spin" /> : <CreditCard className="w-5 h-5" />}
                  <span>{pkg.name === 'Silver' ? 'Current' : 'Upgrade Now'}</span>
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}